.floating-label input, .floating-label textarea{
    background-color: transparent;
    outline: none !important;
    border-color: #fff;
    color:#fff;
    margin-bottom:25px;
}
 
.submit{

    color:#333;
    background: #f0ad4e;
    border: 1px solid #eea236;
   font-weight: 600;
    padding: 10px 20px;
    margin-top:40px;
}
#register {
    background: #222 url('https://unsplash.it/1920/1080/?random') center center no-repeat;
    background-size: cover;
    height: 100%;
    text-align: center;
    display: flex;
    align-items: center;
    position: relative;
    min-height:100vh;
}

#registerForm {
    width: 500px;
    position: relative;
    z-index: 1;
    max-width:100%;
    margin: 0 auto;
    background:rgba(22, 48, 56, 0.75);
    padding:50px;color: #ffffff;
}
.floating-label input:focus, .floating-label textarea:focus {
    border-color:#f0ad4e;
}